import RecentBotEvents from "@/components/tables/RecentBotEvents.vue";
import CommonData from "@/components/common/CommonData.vue";

export default {
  name: "Home",
  components: {
    Header,
    InstaBonusStatus,
    CommonData,
    ShortData,
    SprintData,
    PrepaidSprints,
    CurrentTransactions,
    SuccessfulTransactions,
    FailedTransactions,
    ListRelatives,
    RecentBotEvents,
    History
  },
  data: function() {
    return {
      oldLabel: {
        currentTransactions: "",
        failedTransactions: "",
        successfulTransactions: ""
      },
      currentTransactions: {
        is: false,
        label: "Текущие транзакции"
      },
      failedTransactions: {
        is: false,
        label: "Неудачные транзакции"
      },
      successfulTransactions: {
        is: false,
        label: "Удачные транзакции"
      },
      arrHistory: [
        {
          search: "Коля kolya ht67ed 4333334343 787878",
          name: "Коля",
          nickName: "kolya",
          shortID: "ht67ed",
          telegramID: "4333334343",
          clientID: "787878"
        }
      ]
    };
  },
  computed: {},
  watch: {},
  methods: {
    toggleItem(bool, objStr) {
      if (bool) {
        this.oldLabel[objStr] = this[objStr].label;
        this[objStr].label = "";
      } else {
        setTimeout(() => {
          this[objStr].label = this.oldLabel[objStr];
        }, 200);
      }
    },
    addHistoryItemParent(value) {
      this.arrHistory.unshift(value);
    },
    setUser(value) {}
  },
  mounted() {},
  created() {}
};
</script>

